@charset "utf-8";

.kolonsport-site {
  .layer-point {
    .layer {
      transform: translate(-50%, -50.1%);
    }
    .inner {
      .content {
        padding: 25px 39px;
        h3 {
          margin-top: 23px;
          margin-bottom: 14px;
        }
        li {
          line-height: 1.2;
          &:last-child {
            margin-top: 0;
          }
        }
        table {
          width: 100%;
          margin-top: 14px;
          text-align: center;
          thead {
            th:last-child {
              text-align: left;
              padding-left: 25px;
            }
          }
          tbody {
            td:last-child {
              text-align: left;
              padding-left: 25px;
            }
          }
        }
        .info-txt {
          margin-top: 22px;
          li {
            padding: 0 0 0 14px;
            line-height: 1.7;
            &::before {
              top: 10px;
              left: 7px;
              background-color: #898989;
            }
          }
        }
      }
    }
  }
}
